@import url(https://fonts.googleapis.com/css2?family=Cormorant+Garamond:ital,wght@0,400;1,700&family=Proza+Libre:ital,wght@0,400;1,500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.flex-wrapper{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;width:100%;padding:5px 20px;box-sizing:border-box;position:relative;min-height:60px;background-color:var(--light-beige);color:var(--dark-beige)}.flex-wrapper .nav-left,.flex-wrapper .nav-right{width:50%}.flex-wrapper .nav-left ul,.flex-wrapper .nav-right ul{list-style:none;padding:0}.flex-wrapper .nav-left ul li,.flex-wrapper .nav-right ul li{color:var(--dark-beige);display:inline-block;padding:0 10px;cursor:pointer}.flex-wrapper .nav-left ul li:hover,.flex-wrapper .nav-right ul li:hover{color:var(--black)}@media (max-width: 768px){.flex-wrapper .nav-left,.flex-wrapper .nav-right{display:none}}.flex-wrapper .nav-left{text-align:left}.flex-wrapper .nav-right{text-align:right}.flex-wrapper .nav-center{width:40px;position:absolute;top:5px;left:0}.flex-wrapper .nav-center .nav-logo{width:50px;height:auto;position:absolute;left:calc(50vw - 25px)}@media (max-width: 768px){.nav-desktop{display:none}}@media (min-width: 769px){.nav-mobile{display:none}}.bm-burger-button{position:fixed;width:36px;height:30px;right:36px;top:13px}.bm-burger-bars{background:#373a47}.bm-burger-bars-hover{background:#a90000}.bm-cross-button{height:24px;width:24px}.bm-cross{background:var(--dark-beige)}.bm-menu-wrap{position:fixed;height:100%;top:0}.bm-menu{background:var(--light-beige);padding:2.5em 1.5em 0;font-size:1.15em}.bm-morph-shape{fill:#373a47}.bm-item-list{color:var(--dark-beige);padding:0.8em}.bm-item{display:inline-block;color:var(--dark-beige);padding-top:8px}.bm-overlay{background:rgba(0,0,0,0.3)}

.hero-image{height:50%;background-image:url(/static/media/background.63981096.png);background-position:center;background-repeat:no-repeat;background-size:cover;position:relative}.hero-text{text-align:center;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);color:white}.hero-search-bar input[type=text]{padding:10px;font-size:17px;border:1px solid grey;float:left;width:80%;background:#f1f1f1}.hero-search-bar button{float:left;width:20%;padding:10px;background:#2196F3;color:white;font-size:17px;border:1px solid grey;border-left:none;cursor:pointer}.hero-search-bar button:hover{background:#0b7dda}.form.example::after{content:"";clear:both;display:table}

/* nie wiem co tu dodać bo nie wiem czy bedziemy robic na gridzie czy na flexie */

* {
    box-sizing: border-box;
  }

  body {
    padding: 0;
    margin: 0;
    width: 100vw;
    height: 100vh;
    overflow-x: hidden;
    background: url(/static/media/background.63981096.png) no-repeat center center;
  }

  main {
    padding: 1rem;
    margin: auto;
  }

  .centered {
    text-align: center;
  }

  .App{
    width: 100vw;
    height: 100vh;
  }

  .line-item{
    display: inline-block;
  }

  .item-half{
    padding: 0 20px;
    width: 50%;
    height: 100%;
  }
html {
    font-size:10px;
  }
  
  body {
    font-size:1.5rem;
    color: var(--black);
  }
  
  h1, h2, h3, h4, h5, h6 {
    font-size: 5rem;
    font-family: 'Cormorant Garamond', serif;
    font-weight: 600;
    letter-spacing:-1px;
  }
  
  a {
    text-decoration: none;
    color: var(--darker-blue);
  }
  
  p a {
    font-family: 'Proza Libre', sans-serif;
    border-bottom: 2px solid var(--dark-grey);
  }
  
  p {
    line-height: 2;
    font-size: 1.6rem;
  }

:root {
    --light-blue: #D0E2F2;
    --darker-blue: #BDD9F2;
    --light-green: #BFB741;
    --darker-green: #8C862E;
    --beige: #BFA79F;
    --dark-grey: #878B8C;
    --black: #0B0D0C;
    --light-beige: #e3cdc6;
    --dark-beige: #786a66;
    --shadow: 0 0 10px rgba(0,0,0,0.1), 0 5px 10px rgba(0,0,0,0.05);
  }
